body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


td {
  padding: 2px;
  padding-left: 1em;
  border: solid 1px  rgb(4, 42, 92);
  color: rgb(16, 9, 110);
  font-weight: bold;
}

.songlist {
  border-collapse: collapse;
  background-color: rgb(248, 210, 200);
  font-size: 1rem;
  text-align: left;
  border: solid 2px;
  width: 98%
}

.linkColumn{
  padding-left: 3em;
  padding-right: 3em;
  background-color: rgb(192, 224, 208);
}

a:link {
  color: rgb(94, 138, 99);
  font-style: italic;
  text-decoration: underline;
  font-size: 1.2rem;
}

a:visited {
  color: rgb(19, 59, 59);
  background-color: transparent;
  text-decoration: underline;
}

a:hover {
  color: rgb(161, 218, 221);
  background-color: transparent;
  text-decoration: underline;
}

input {
  font-size: large;
  margin-bottom: 4px;
}

.bigger {
  font-size: x-large;
}

.messageTable {
  border-collapse: collapse;
  background-color: rgb(32, 96, 180);
  text-align: left;
  border: solid 2px;
}

.SLTH { /* songlistTableHeader */
  font-size: 1.4rem;
  background-color: rgb(201, 215, 219);
}

.minWidth3 {
  min-width: 30%;
}

.width05 { width: 5% }
.width10 { width: 10% }
.width20 { width: 20% }
.width30 { width: 30% }
.width40 { width: 40% }

.width95 {
  width: 95%;
}

.nr {
  min-width: 20px;
  max-width: 30px;
}


@media only screen and (min-width: 768px) {
  .SLTH { /* songlistTableHeader */
    font-size: 1.1REM;
    width: 20px;
  }
  
  .songlist {
    font-size: 1.1rem;
    border: solid 1px;
  }
  
  a:link {
    font-size: 1.3rem;
  }
}
th {
  border: solid 2px rgb(181, 183, 196); 
  padding-left: 1em;
  font-size: small;
  font-weight: bolder;
}

td {
  padding: 2px;
  padding-left: 1em;
  border: solid 1px  rgb(129, 145, 156);
  max-width: 20%; 
}

table {
  border-collapse: collapse;
  text-align: left;
  border: solid 2px;
